import { useState, useEffect } from 'react'
import './staking.less'
import logo from "../img/logo-deri.svg"
import Slider from '../../../components/Slider/TimeSlider'
import Cliam from './Claim'
import apiProxy from '../../../model/ApiProxy'
export default function Staking({ lang }) {
  const [isLock, setIsLock] = useState({})
  const [nowTimestamp, setNowTimestamp] = useState(0)
  const [remainingTime, setRemainingTime] = useState('')
  const startTimestamp = 1638698400
  const endTimestamp = 1638957600
  useEffect(() => {
    // let interval = null;
    // interval = window.setInterval(() => {
    //   let now = parseInt(Date.now() / 1000)
    //   let dis = endTimestamp - now
    //   let d = parseInt(dis / 60 / 60 / 24, 10);
    //   let h = parseInt(dis / 60 / 60 % 24, 10);
    //   let m = parseInt(dis / 60 % 60, 10);
    //   setRemainingTime(`${d} d ${h} h ${m} m`);
    //   setNowTimestamp(now)
    // }, 1000);
    getStakingAddressCount()
    // return () => {
    //   interval && clearInterval(interval);
    // };
  }, [])
  const getStakingAddressCount = async () => {
    let res = await apiProxy.request('getStakingAddressCount', [3])
    let obj = {
      one: true,
      two: false,
      three: false,
      four: false,
    }
    if (res.addressCount > 1500) {
      obj.two = true
    }
    if (res.addressCount > 3000) {
      obj.three = true
    }
    if (res.addressCount > 5000) {
      obj.four = true
    }
    setIsLock(obj)
  }
  return (
    <div className='staking'>
      <div className='title-timeout'>
        <div className='title-time-text'>
          <div className='title-text'>
            <div className='title-epoch'>
              {lang["title"]} (Epoch 3)
            </div>
            <div className='time xs-hide'>
              {/* {remainingTime} */}
            </div>
          </div>
          <div className='start-end-time-epoch'>
            <div className='satrt-end-time'>
              10:00:00 AM, December 8th 2021  to 10:00:00 AM, January 5th 2022  UTC
            </div>
            <div className='epoch xs-hide'>
              {/* Epoch 3 Countdown */}
            </div>
          </div>
          {/* <div className='slider'>
            <Slider startTimestamp={startTimestamp} nowTimestamp={nowTimestamp} endTimestamp={endTimestamp} />
          </div> */}
          <div className='xs-show'>
            <div className='epoch'>
              {/* Epoch 3 Countdown */}
            </div>
            <div className='time'>
              {/* {remainingTime} */}
            </div>
          </div>
        </div>
      </div>
      <div className='claim'>
        <Cliam lang={lang} />
      </div>
      <div className='rewards-box'>
        <div className='total-rewards-pool'>
          <div className='deri-logo-bg'>
            <img alt='' src={logo} />
          </div>
          <div className='total-rewards-num'>
            <div className='total-rewards-pool-title'>
              TOTAL REWARDS POOL
            </div>
            <div className='toatl-rewards-deri'>
              <span>2,000,000</span>
              <span className='deri-text'>DERI</span>
            </div>
          </div>
        </div>
        <div className='open-lock-rewards'>
          <div className='top-open-lock-box'>
            <div className='lock-open open'>
              <div className='lock-logo open-logo '>
                OPEN
              </div>
              <div className='address-num'>
                &lt; 1500 addresses
              </div>
              <div className='address-reward'>
                <span>500,000 </span>  <span className='deri-text'>DERI</span>
              </div>
              <div className='percentage-reward'>
                25% UNLOCKED
              </div>
            </div>
            <div className={isLock.two ? 'lock-open open' : 'lock-open'}>
              <div className= {isLock.two ? 'lock-logo open-logo' : 'lock-logo '} >
                {isLock.two ? 'OPEN' : 'LOCK'}
              </div>
              <div className='address-num'>
                1,500 - 3000 addresses
              </div>
              <div className='address-reward'>
                <span>1,000,000 </span>  <span className='deri-text'>DERI</span>
              </div>
              <div className='percentage-reward'>
                50% UNLOCKED
              </div>
            </div>
          </div>
          <div className='bottom-open-lock-box'>
            <div className={isLock.three ? 'lock-open open' : 'lock-open'}>
              <div className= {isLock.three ? 'lock-logo open-logo' : 'lock-logo'} >
                {isLock.three ? 'OPEN' : 'LOCK'}
              </div>
              <div className='address-num'>
               3000 - 5000 addresses
              </div>
              <div className='address-reward'>
                <span>1,500,000 </span>  <span className='deri-text'>DERI</span>
              </div>
              <div className='percentage-reward'>
                75% UNLOCKED
              </div>
            </div>
            <div className={isLock.four ? 'lock-open open' : 'lock-open'}>
              <div className={isLock.four ? 'lock-logo open-logo' : 'lock-logo'}>
                {isLock.four ? 'OPEN' : 'LOCK'}
              </div>
              <div className='address-num'>
                &gt; 5,000 addresses
              </div>
              <div className='address-reward'>
                <span>2,000,000 </span>  <span className='deri-text'>DERI</span>
              </div>
              <div className='percentage-reward'>
                100% UNLOCKED
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className='stage-points'>
        <div className='stage-points-info'>
          <div className='top-stage-points'>
            <div className='points-box'>
              <div className='stage-box'>
                1 ST
              </div>
              <div className='points-num'>
                10,000 POINTS
              </div>
              <div className='start-end-stage-time'>
                10 AM, Dec 8  - 10 AM, Dec 15 UTC
              </div>
            </div>
            <div className='points-box xs-last'>
              <div className='stage-box'>
                2 ND
              </div>
              <div className='points-num'>
                20,000 POINTS
              </div>
              <div className='start-end-stage-time'>
                10 AM, Dec 15  - 10 AM, Dec 22 UTC
              </div>
            </div>
          </div>
          <div className='bottom-stage-points'>
            <div className='points-box'>
              <div className='stage-box'>
                3 RD
              </div>
              <div className='points-num'>
                30,000 POINTS
              </div>
              <div className='start-end-stage-time'>
                10 AM, Dec 22  - 10 AM, Dec 29 UTC
              </div>
            </div>
            <div className='points-box last xs-last'>
              <div className='stage-box'>
                4 TH
              </div>
              <div className='points-num'>
                50,000 POINTS
              </div>
              <div className='start-end-stage-time'>
                10 AM, Dec 29  - 10 AM, Jan 5 UTC,2022
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}