import { useState, useEffect } from 'react'
import { inject, observer } from 'mobx-react';
import './claim.less'
import { useAlert } from 'react-alert'
import {bg} from '../../../lib/web3js/index'
import DeriNumberFormat from "../../../utils/DeriNumberFormat"
import apiProxy from "../../../model/ApiProxy"
import Button from '../../../components/Button/Button';
function Claim({ wallet, lang }) {
  const [claimRewardDeri, setClaimRewarDeri] = useState('')
  const [claimedDeri, setClaimedDeri] = useState('')
  const [lockedDeri, setLockedDeri] = useState('')
  const [claimableDeri, setClaimableDeri] = useState('')
  const [disabled,setDisabled] = useState(true)
  const alert = useAlert();
  const getClaimInfo = async ()=>{
    let res = await apiProxy.request('getUserStakingClaimInfo',[wallet.detail.account, 3])
    if(res){
      let total = (+res.claimed) + (+res.locked) + (+res.claimable)
      setClaimedDeri(res.claimed)
      setLockedDeri(res.locked)
      setClaimableDeri(res.claimable)
      setClaimRewarDeri(total)
    }
  }

  const claim = async ()=>{
    if(+claimableDeri.claimable>0){
      return;
    }
    let nowSixEnd = new Date(new Date().toLocaleDateString()).getTime() + 18.5 * 60 * 60 * 1000
    let nowSix = new Date(new Date().toLocaleDateString()).getTime() + 18 * 60 * 60 * 1000
    let now = parseInt(Date.now())
    if (now < nowSixEnd && now > nowSix) {
      alert.error('Claiming DERI is disabled during first 30 minutes of each epoch')
      return;
    }
    let res = await apiProxy.request('claimMyStaking',[wallet.detail.account, 3],{ includeResponse: true })
    if (!res || !res.success) {
      alert.error('Claim fail');
      return false;
    }else{
      alert.success('Claim success')
      let claimed = bg(claimedDeri).plus(bg(claimableDeri)).toString()
      setClaimedDeri(claimed)
      setClaimableDeri(0)
    }
  }

  useEffect(()=>{
    if(+claimableDeri > 0){
      setDisabled(false)
    }else{
      setDisabled(true)
    }
  },[claimableDeri])

  useEffect(()=>{
    if(wallet.isConnected()){
      getClaimInfo()
    }
  },[wallet,wallet.detail])

  return (
    <div className='trade-claim'>
      <div className='rewards-claim'>
        {lang['rewards']}
      </div>
      <div className='rewards-info'>
        <div className='lg-rewards-info'>
          <div className='my-rewards'>
            <div className='claim-title'>
              {lang['my-rewards']}
            </div>
            <div className='claim-num'>
              <DeriNumberFormat value={claimRewardDeri} decimalScale={2} /> DERI
            </div>
          </div>
          <div className='claimed-deri'>
            <div className='claim-title'>
              {lang['claimed-deri']}
            </div>
            <div className='claim-num'>
              <DeriNumberFormat value={claimedDeri} decimalScale={2} />
            </div>
          </div>
          <div className='locked-deri'>
            <div className='claim-title'>
              {lang['lcked-deri']}
            </div>
            <div className='claim-num'>
              <DeriNumberFormat value={lockedDeri} decimalScale={2} />
            </div>
          </div>
          <div className='claimable-deri'>
            <div className='claim-title'>
              {lang['claimable-deri']}
            </div>
            <div className='claim-num'>
              <DeriNumberFormat value={claimableDeri} decimalScale={2} />
            </div>
          </div>
          <div className='cliam-button'>
            <Button width='102' height='34' label={lang['claim']} disabled={disabled} onClick={()=>claim()} />
          </div>
        </div>
        <div className='md-rewards-info'>
          <div className='top-rewards-info'>
            <div className='my-rewards'>
              <div className='claim-title'>
                {lang['my-rewards']}
              </div>
              <div className='claim-num'>
                <DeriNumberFormat value={claimRewardDeri} decimalScale={2} /> DERI
            </div>
            </div>
            <div className='cliam-button'>
              <Button width='102' height='34' label={lang['claim']} disabled={disabled} onClick={()=>claim()} />
            </div>
          </div>
          <div className='bottom-rewards-info'>
            <div className='claimed-deri'>
              <div className='claim-title'>
                {lang['claimed-deri']}
              </div>
              <div className='claim-num'>
                <DeriNumberFormat value={claimedDeri} decimalScale={2} />
              </div>
            </div>
            <div className='locked-deri'>
              <div className='claim-title'>
                {lang['lcked-deri']}
              </div>
              <div className='claim-num'>
                <DeriNumberFormat value={lockedDeri} decimalScale={2} />
              </div>
            </div>
            <div className='claimable-deri'>
              <div className='claim-title'>
                {lang['claimable-deri']}
              </div>
              <div className='claim-num'>
                <DeriNumberFormat value={claimableDeri} decimalScale={2} />
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  )
}
export default inject('wallet')(observer(Claim))