import React, { useState, useEffect } from 'react'
import DeriNumberFormat from '../../../utils/DeriNumberFormat'
export default function PnlTop({ lang, List }) {
  return (
    <div className='ponts-pnl-top-ten'>
      <div className='list-box'>
        <div className='list-box-title'>
          <span className='no'>{lang['no']}</span>
          <span className='address'>{lang['user-addr']}</span>
          <span className='pnl'>PnL</span>
          {/* <span className='rewardBNB'>Reward</span> */}
        </div>
        <div className='list-info'>
          {List.map((item, index) => {
            return (
              <div className='list-info-box' key={index}>
                <div className='no'>
                  {item.no === 1 && <span className='top-three-no'>1</span>}
                  {item.no === 2 && <span className='top-three-no'>2</span>}
                  {item.no === 3 && <span className='top-three-no'>3</span>}
                  {item.no !== 1 && item.no !== 2 && item.no !== 3 && item.no}
                </div>
                <div className='address'>
                  {item.userAddr}
                </div>
                <div className='pnl'>
                  $ <DeriNumberFormat value={item.pnl} decimalScale={2} thousandSeparator={true} />
                </div>
                {/* <div className={(item.no === 1 || item.no === 2 || item.no === 3) ? 'rewardBNB top-three' : "rewardBNB"}>
                  $ <DeriNumberFormat value={item.specialRewardsB} thousandSeparator={true} />
                </div> */}
              </div>
            )
          })}
        </div>
      </div>
    </div>
  )
}